
.card {
  background: #fff;
  border-radius: 2px;
  padding: 30px 30px 30px 30px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 325px;
  box-shadow: 0 3px 6px black, 0 3px 6px #999;
  /* text-align: left; */
}

.card > .img-container {
  height: 80%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
  
}
.card > .img-container > img {
  width: 100%;
  height: 100%;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}


.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}



